"use client";
import { useRef, useEffect } from "react";
import { Chart } from "chart.js/auto";
import { CHART_COLORS } from "chart.js"; // اگر Utils در دسترس نیست

export default function LineChart() {
  const chartRef = useRef<HTMLCanvasElement | null>(null);

  useEffect(() => {
    if (!chartRef.current) return;

    const ctx = chartRef.current.getContext("2d");
    if (!ctx) return;

    // اگر نموداری قبلی بود، نابودش کن
    if (chartRef.current.chart) {
      chartRef.current.chart.destroy();
    }

    let width: number, height: number;
    let gradient: CanvasGradient;

    function getGradient() {
      const chart = chartRef.current?.chart;
      if (!chart) return;
      const chartArea = (chart as any).chartArea;
      if (!chartArea) return;

      const chartWidth = chartArea.right - chartArea.left;
      const chartHeight = chartArea.bottom - chartArea.top;

      if (!gradient || width !== chartWidth || height !== chartHeight) {
        width = chartWidth;
        height = chartHeight;
        gradient = ctx.createLinearGradient(0, chartArea.bottom, 0, chartArea.top);
        gradient.addColorStop(0, CHART_COLORS.blue);
        gradient.addColorStop(0.5, CHART_COLORS.yellow);
        gradient.addColorStop(1, CHART_COLORS.red);
      }
      return gradient;
    }

    const newChart = new Chart(ctx, {
      type: "line",
      data: {
        labels: [0, 5, 10, 15, 20, 25, 30, 35, 40, 45, 50],
        datasets: [
          {
            label: "Temperature",
            data: [24, 27, 30, 18, 20, 17, 25, 24, 19, 26],
            fill: true,
            backgroundColor: "rgba(255, 99, 132, 0.2)",
            borderColor: function (context) {
              return getGradient() as any;
            },
            borderWidth: 3,
            tension: 0.4,
            pointRadius: 6,
            pointHoverRadius: 8,
          },
        ],
      },
      options: {
        responsive: true,
        plugins: {
          legend: { display: true },
        },
        scales: {
          x: { title: { display: true, text: "Time" } },
          y: { beginAtZero: true, title: { display: true, text: "Value" } },
        },
      },
    });

    chartRef.current.chart = newChart;
  }, []);

  return (
    <div style={{ position: "relative", width: "90vw", height: "80vh" }}>
      <canvas ref={chartRef} />
    </div>
  );
}
